import React from 'react';
import styled from 'styled-components';

import { Link } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

interface TitleProps {
  title: string;
  url: string;
}

const Wrapper = styled.div`
  width: 100%;
  margin-left: 15px;
  text-overflow: ellipsis;

  @media (max-width: 720px) {
    margin: 15px 0 5px;
  }
`;

const Title = styled.span`
  font-size: 18px;
  font-weight: 500;
  color: #222;

  &:hover {
    text-decoration: underline;
  }

  @media (max-width: 1200px) {
    font-size: 13px;
  }

  @media (max-width: 720px) {
    font-size: 26px;
    font-weight: bold;
  }
`;

const WeeklyTitle: React.FC<TitleProps> = ({ title, url }) => {
  const ga = createGA('Main - Weekly Picks', 'click', 'weekly pick post', title);

  return (
    <Wrapper>
      <Link type="internal" url={url} ga={ga}>
        <Title>{title}</Title>
      </Link>
    </Wrapper>
  );
};

export default WeeklyTitle;
