import React from 'react';
import styled from 'styled-components';

import { ReferenceItem } from '@t/components';
import { InlineBlockLink } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

interface ReferenceSectionProps {
  items: ReferenceItem[];
}

interface ReferenceProps {
  item: ReferenceItem;
}

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
  background-color: #deeeff;

  @media (max-width: 720px) {
    display: none;
  }
`;

const ReferenceTitle = styled.div`
  font-size: 22px;
  font-weight: 500;
  line-height: 1.91;
  color: #222;
  margin-bottom: 30px;

  @media (max-width: 1200px) {
    font-size: 16px;
  }
`;

const ReferenceLogo = styled.img`
  height: 75px;
  margin: 0 25px;

  @media (max-width: 1200px) {
    height: 56px;
    margin: 0 19px;
  }
`;

const Reference: React.FC<ReferenceProps> = ({ item: { link, logo, name } }) => {
  const ga = createGA('Main - Reference', 'click', 'Reference logo', name);

  return (
    <InlineBlockLink type="external" url={link} ga={ga} testId="reference-list">
      <ReferenceLogo src={logo} alt={`${name} Logo`} title={name} />
    </InlineBlockLink>
  );
};

const ReferenceSection: React.FC<ReferenceSectionProps> = ({ items }) => {
  const referenceItems = items.map(({ id, name, link, logo }) => ({ id, name, link, logo }));

  return (
    <Wrapper>
      <ReferenceTitle>Who's using TOAST UI?</ReferenceTitle>
      <div>
        {referenceItems.map((item, index) => (
          <Reference item={item} key={index} />
        ))}
      </div>
    </Wrapper>
  );
};

export default ReferenceSection;
