import React from 'react';
import styled from 'styled-components';

import { Link } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

// TODO : 4.1 업데이트 시 적용 예정
// type Url = '/fe-guide/' | '/weekly-pick/';
//
// type Language = 'ko' | 'en';
