interface SeeAllLinkProps {
  url: '/fe-guide/ko' | '/fe-guide/en' | '/weekly-pick/ko' | '/weekly-pick/en';
}

const LinkContent = styled.span`
  color: #526fff;

  &:hover {
    text-decoration: underline;
  }
`;

const SeeAllLink: React.FC<SeeAllLinkProps> = ({ url }) => {
  const ga = createGA('Main - see all(FE Guide, Weekly Picks', 'click', 'See all link', url);

  return (
    <Link type="internal" url={url} ga={ga}>
      <LinkContent>see all</LinkContent>
    </Link>
  );
};

export default SeeAllLink;
