import React from 'react';
import styled from 'styled-components';

interface DateProps {
  date: string;
}

const Wrapper = styled.div`
  width: 104px;
  font-size: 16px;
  color: #777;
  text-align: end;

  @media (max-width: 1200px) {
    width: 100px;
    font-size: 12px;
  }

  @media (max-width: 720px) {
    width: 120px;
    font-size: 22px;
    text-align: start;
  }
`;

const Date: React.FC<DateProps> = ({ date }) => {
  return <Wrapper>{date}</Wrapper>;
};

export default Date;
