import React from 'react';
import styled from 'styled-components';

import { ApplicationId } from '@t/components';

interface PreviewProps {
  id: ApplicationId;
}

interface ImageProps {
  src: string;
}

interface ImageBackgroundProps {
  url: string;
}

const PreviewArea = styled.div`
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
  border: solid 1px #ddd;
  border-radius: 2px;
`;

const WebPreviewArea = styled(PreviewArea)`
  width: 740px;
  height: 520px;

  @media (max-width: 1200px) {
    width: 555px;
    height: 405px;
  }

  @media (max-width: 768px) {
    display: none;
  }
`;

const MobilePreviewArea = styled(PreviewArea)`
  display: none;

  @media (max-width: 768px) {
    display: block;
    height: 405px;
    margin-bottom: 30px;
  }

  @media (max-width: 720px) {
    height: 470px;
    margin-bottom: 60px;
  }
`;

const Header = styled.div`
  height: 22px;
  background-color: #e5e5e5;
  padding-top: 8px;
  text-align: left;

  @media (max-width: 1200px) {
    height: 23px;
    padding-top: 6px;
  }

  @media (max-width: 720px) {
    height: 23px;
    padding-top: 8px;
  }
`;

const HeaderDecoration = styled.div`
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #eee;
  margin-left: 8px;

  @media (max-width: 1200px) {
    width: 11px;
    height: 11px;
  }
`;

const ImageArea = styled.div`
  height: calc(100% - 29px);
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 1200px) {
    height: calc(100% - 30px);
  }

  @media (max-width: 720px) {
    height: 438px;
  }
`;

const Image = styled.div<ImageBackgroundProps>`
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  background: url(${({ url }): string => url}) center/contain no-repeat;
`;

const Preview: React.FC<ImageProps> = ({ src }) => {
  const url = `https://uicdn.toast.com/toastui/img/${src}.png`;

  return (
    <>
      <Header>
        <HeaderDecoration />
        <HeaderDecoration />
        <HeaderDecoration />
      </Header>
      <ImageArea>
        <Image url={url} />
      </ImageArea>
    </>
  );
};

export const WebPreview: React.FC<PreviewProps> = ({ id }) => (
  <WebPreviewArea>
    <Preview src={id} />
  </WebPreviewArea>
);

export const MobilePreview: React.FC<PreviewProps> = ({ id }) => (
  <MobilePreviewArea>
    <Preview src={`${id}_mobile`} />
  </MobilePreviewArea>
);
